import React from "react";

class AddCourses extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { toggleInputNewCourse } = this.props;
    return (
      <div id="AddCourse" onClick={toggleInputNewCourse}>
        <p id="AddCoursePlus">+</p>
        <p id="AddCourseDesc">Añadir curso</p>
      </div>
    );
  }
}

export default AddCourses;
