import React from "react";
import AddCourses from "./AddCourse";
import CourseCard from "./CourseCard";

class Courses extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const {
      dbCourses,
      toggleInputNewCourse,
      deleteCourse,
      deleteNote,
      addNewNote,
    } = this.props;
    return (
      <div id="Courses">
        {dbCourses.map((i, index) => (
          <CourseCard
            key={index}
            name={i.name}
            notes={i.notes}
            deleteCourse={deleteCourse}
            deleteNote={deleteNote}
            addNewNote={addNewNote}
          ></CourseCard>
        ))}
        <AddCourses toggleInputNewCourse={toggleInputNewCourse}></AddCourses>
      </div>
    );
  }
}

export default Courses;
