import React from "react";

class InputNewCourse extends React.Component {
  constructor(props) {
    super(props);
    this.state = { CourseName: "" };
  }

  render() {
    const { visibility, toggleInputNewCourse, createNewCourse } = this.props;
    return (
      <div
        id="InputNewCourse"
        style={visibility ? { display: "flex" } : { display: "none" }}
      >
        <input
          id="InputNewCourseInput"
          placeholder="Nombre del curso"
          onChange={(e) => this.setState({ CourseName: e.target.value })}
        ></input>
        <button
          id="InputNewCourseButton"
          onClick={() => {
            createNewCourse(this.state.CourseName);
          }}
        >
          Añadir
        </button>
        <button id="InputNewCourseButtonCancel" onClick={toggleInputNewCourse}>
          Cancelar
        </button>
      </div>
    );
  }
}

export default InputNewCourse;
