import React from "react";
import Note from "./Note";

class Notes extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { notes, deleteNote, keyCard } = this.props;
    return (
      <div id="Notes">
        <div id="NotesHeader">
          <p>Nota</p>
          <p>Valor (%)</p>
        </div>
        {notes.map((w, index) => (
          <Note
            key={index}
            keyCard={keyCard}
            note={w.note}
            value={w.value}
            deleteNote={deleteNote}
          ></Note>
        ))}
      </div>
    );
  }
}

export default Notes;
