import React from "react";
import { FaTimes } from "react-icons/fa";

class Note extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { note, value, deleteNote, keyCard } = this.props;
    return (
      <div id="Note">
        <div id="NoteData">
          <p>{note}</p>
          <p>{value}</p>
        </div>
        <p id="NoteDelete">
          <FaTimes
            onClick={() => {
              deleteNote(this._reactInternalFiber.key, keyCard);
            }}
          />
        </p>
      </div>
    );
  }
}

export default Note;
