import React from "react";
import { FaRocket } from "react-icons/fa";

class Header extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div id="Header">
        <p id="HeaderH1">
          <FaRocket size={20} color="#0400ff" />
          Rockete
        </p>
      </div>
    );
  }
}

export default Header;
