import React from "react";
import Courses from "./Courses";
import InputNewCourse from "./InputNewCourse";

class Wrapper extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      dbCourses: [],
      InputNewCourse: false,
    };
  }

  componentDidMount() {
    const lastDbState = JSON.parse(localStorage.getItem("dbCourses"));
    this.setState({ dbCourses: lastDbState });
    console.log(lastDbState);
  }

  createNewCourse = (CourseName) => {
    this.setState({
      dbCourses: [...this.state.dbCourses, { name: CourseName, notes: [] }],
      InputNewCourse: !this.state.InputNewCourse,
    });
    const { dbCourses } = this.state;
    localStorage.setItem("dbCourses", JSON.stringify(dbCourses));
  };

  addNewNote = (NoteValue, PercValue, Ixx) => {
    let dbState = this.state.dbCourses;
    let lastNotes = dbState[Ixx].notes;
    let newNotes = [
      ...lastNotes,
      { note: Number(NoteValue), value: Number(PercValue) },
    ];
    dbState[Ixx].notes = newNotes;
    this.setState({ dbCourses: dbState });
    const { dbCourses } = this.state;
    localStorage.setItem("dbCourses", JSON.stringify(dbCourses));
  };

  deleteCourse = (ic) => {
    this.setState({ ...this.state.dbCourses.splice(ic, 1) });
    const { dbCourses } = this.state;
    localStorage.setItem("dbCourses", JSON.stringify(dbCourses));
  };

  deleteNote = (inn, inx) => {
    let dbCourse = this.state.dbCourses;
    let dbCourseIndex = dbCourse[inx];
    let lastNotes = dbCourseIndex.notes;
    lastNotes.splice(inn, 1);
    dbCourse[inx].notes = lastNotes;
    this.setState({ dbCourse: dbCourse });
    const { dbCourses } = this.state;
    localStorage.setItem("dbCourses", JSON.stringify(dbCourses));
  };

  toggleInputNewCourse = () => {
    this.setState({ InputNewCourse: !this.state.InputNewCourse });
    const { dbCourses } = this.state;
    localStorage.setItem("dbCourses", JSON.stringify(dbCourses));
  };

  render() {
    return (
      <div id="Wrapper">
        <h1 id="WrapperH4">Tus cursos</h1>
        <Courses
          dbCourses={this.state.dbCourses}
          toggleInputNewCourse={this.toggleInputNewCourse}
          deleteCourse={this.deleteCourse}
          deleteNote={this.deleteNote}
          addNewNote={this.addNewNote}
        ></Courses>
        <InputNewCourse
          visibility={this.state.InputNewCourse}
          toggleInputNewCourse={this.toggleInputNewCourse}
          createNewCourse={this.createNewCourse}
        ></InputNewCourse>
      </div>
    );
  }
}

export default Wrapper;
