import React from "react";
import Notes from "./Notes";
import { FaTimes } from "react-icons/fa";

class CourseCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = { NoteValue: "", PercValue: "  " };
  }

  getXnP = (notes) => {
    let Pond = [];
    let Perc = [];
    let Xb;
    let Pc;
    notes.map((i) => {
      Pond.push(i.note * (i.value / 100));
      Perc.push(i.value);
    });

    if (Pond < 3) {
      const reducer = (xi, xi1) => xi + xi1;
      Pc = 100 - Perc.reduce(reducer);
      Xb = ((3 - Pond.reduce(reducer)) * 100) / Pc + 0.01;

      return `Necesitas ${Xb.toFixed(2)} en el ${Pc.toFixed(2)}%`;
    } else {
      const reducer = (xi, xi1) => xi + xi1;
      Xb = Pond.reduce(reducer);
      return `¡Ya ganaste! Tu nota es: ${Xb.toFixed(2)}`;
    }

    return [Xb.toFixed(2), Pc];
  };

  render() {
    const { name, notes, deleteCourse, deleteNote, addNewNote } = this.props;
    return (
      <div id="CourseCard">
        <div id="CourseCardHeader">
          <h5>{name}</h5>
          <FaTimes
            onClick={() => {
              deleteCourse(this._reactInternalFiber.key);
            }}
          />
        </div>
        <div id="CourseCardNotesWrapper">
          {notes.length > 0 ? (
            <Notes
              keyCard={this._reactInternalFiber.key}
              deleteNote={deleteNote}
              notes={notes}
            ></Notes>
          ) : (
            <p>No has añadido notas</p>
          )}
        </div>
        <div id="CourseCardAddNote">
          <input
            id="CourseCardNoteInput"
            placeholder="Nota"
            type="number"
            max="5"
            min="0"
            onChange={(e) => this.setState({ NoteValue: e.target.value })}
          ></input>
          <input
            id="CourseCardPercInput"
            placeholder="Valor %"
            type="number"
            max="100"
            min="0"
            onChange={(e) => this.setState({ PercValue: e.target.value })}
          ></input>
          <button
            id="CourseCardNoteButton"
            onClick={() => {
              addNewNote(
                this.state.NoteValue,
                this.state.PercValue,
                this._reactInternalFiber.key
              );
            }}
          >
            +
          </button>
        </div>
        <div id="CourseCardInfo">
          {notes.length > 0 ? `${this.getXnP(notes)} ` : "..."}
        </div>
      </div>
    );
  }
}

export default CourseCard;
